  <template>
    <div class="app-container">
        <el-button type="primary" @click="addWhiteList">点击添加白名单</el-button>
        <el-table
            :data="tableData"
            style="width: 100%">
            <el-table-column
                prop="id"
                label="id"
                width="180">
            </el-table-column>
            <el-table-column
                prop="client_ip"
                label="白名单ip"
                width="180">
            </el-table-column>
            <el-table-column
                prop="note"
                label="备注"
                width="180">
            </el-table-column>
            <el-table-column
                prop="add_time"
                label="添加时间"
                width="180">
            </el-table-column>
            <el-table-column
                prop="adder"
                label="添加人"
                width="180">
            </el-table-column>
            <el-table-column 
                label="操作">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>

  </template>

<script>
import { getWhiteList } from '@/api/login';
import { deleteWhiteListItem } from '@/api/login';
import { dateFtt } from '@/utils/index';

export default {
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      totalCount: 0,
      tableData: []
    };
  },
  methods: {
    formatter(row, column) {
      return row.address;
    },
    addWhiteList() {
      this.$router.push({ path: 'whitelistadd' });
    },
    handleDelete(index, row) {
      deleteWhiteListItem(row.id).then(response => {
        if (response.Status === 200) {
          location.reload();
        }
      });
    },
    filterSendType(value, row) {
      return row.type === value;
    },
    getWhiteList() {
      getWhiteList().then(response => {
        this.tableData = response.Data.whiteList;
        this.tableData.forEach(element => {
          element['add_time'] = dateFtt(
            'yyyy-MM-dd hh:mm:ss',
            new Date(element['add_time'])
          );
        });
      });
    }

  },
  created() {
    this.getWhiteList();
  }
};
</script>


